const debounceRAF = (() => {
  let currentSeq = 0;
  return (fn, ...args) => {
    const seq = ++currentSeq;
    requestAnimationFrame(() => {
      if (fn && currentSeq === seq) {
        fn(...args);
      }
    });
    return {
      release() {
        fn = null;
        return null;
      },
    };
  };
})();

export default debounceRAF;
