import { render } from 'lit-html';
import debounceRAF from './debounce-raf';
import navPage from './pages/nav';
import introductionPage from './pages/introduction';
import sigmaPage from './pages/sigma';
import iNotesPage from './pages/inotes';
import liteModePage from './pages/lite-mode';
import fullModePage from './pages/full-mode';
import ibmCloudConsolePage from './pages/ibm-cloud-console';
import carbonDesignSystemPage from './pages/carbon-design-system';

document.addEventListener('as-portfolio-rendered', () => {
  debounceRAF(() => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  });
});

/**
 * Renders the page with the given name.
 * @param {string} name The name of the page.
 */
async function renderPage(name) {
  const templates = {
    nav: navPage,
    introduction: introductionPage,
    'sigma': sigmaPage,
    'inotes': iNotesPage,
    'lite-mode': liteModePage,
    'full-mode': fullModePage,
    'ibm-cloud-console': ibmCloudConsolePage,
    'carbon-design-system': carbonDesignSystemPage,
  };
  const beforeRenderedEvent = new CustomEvent('as-portfolio-beingrendered', {
    bubbles: true,
    cancelable: true,
    detail: {
      name,
    },
  });
  if (document.dispatchEvent(beforeRenderedEvent)) {
    render(templates[name], document.querySelector(name === 'nav' ? 'bx-side-nav-items' : 'main'));
    await 0;
    const afterRenderedEvent = new CustomEvent('as-portfolio-rendered', {
      bubbles: true,
      cancelable: true,
      detail: {
        name,
      },
    });
    document.dispatchEvent(afterRenderedEvent);
  }
};

export default renderPage;
