import renderPage from './render-page';

function navigateTo(navName) {
  window.history.pushState({ navName }, navName, `./?page=${navName}`);
  const itemToActivate = document.querySelector(`[data-nav-name="${navName}"]`);
  if (itemToActivate) {
    for (const item of document.querySelectorAll('bx-side-nav-menu-item[active],bx-side-nav-link[active],bx-side-nav-menu[active]')) {
      item.active = false;
    }
    itemToActivate.active = true;
    const parent = itemToActivate.closest('bx-side-nav-menu');
    if (parent) {
      parent.active = true;
    }
    for (const item of document.querySelectorAll('bx-side-nav-menu')) {
      item.expanded = item.contains(itemToActivate);
    }
    renderPage(navName);
  }
}

document.addEventListener('click', event => {
  const link = event.target.closest('a');
  const elem = event.target.closest('[data-nav-name]');
  if (!elem || link && elem !== link && elem.contains(link)) {
    event.stopPropagation();
  } else if (elem) {
    const { dataset } = elem;
    const { navName } = dataset;
    if (navName) {
      navigateTo(navName);
      event.preventDefault();
    }
  }
});

window.addEventListener('popstate', event => {
  const { navName } = event.state ?? {};
  if (navName) {
    navigateTo(navName);
  }
});

renderPage('nav').then(() => {
  navigateTo((new URL(document.location)).searchParams.get('page') || 'introduction');
});

export default navigateTo;
